import React, { FC, useMemo } from "react";
import { Row, RowCenter } from "../../../../styled";
import {
  ArrowImg,
  Blue,
  ColorBarWrap,
  Green,
  Num,
  Red,
  TitleWrap,
  Yellow,
} from "./ColorBar.styled";

interface IColorBar {
  redPercent: number;
  yellowPercent: number;
  bluePercent: number;
  greenPercent: number;
  num: number;
}

export const barWith = 1000;

// 13->10  13*10/13

const getWith = (number: number) => {
  return Math.floor((number * 1000) / 100);
};

export const ColorBar: FC<IColorBar> = ({
  redPercent,
  greenPercent,
  yellowPercent,
  bluePercent,
  num,
}) => {
  const numArr = useMemo(
    () => [100, 200, 300, 400, 500, 600, 700, 800, 900, 1000, 1100, 1200, 1300],
    []
  );

  const demoColor = useMemo(
    () => [
      {
        color: "red",
        text: "Urgent Intervention",
      },
      {
        color: "yellow",
        text: "Intervention",
      },
      {
        color: "blue",
        text: "On Watch",
      },
      {
        color: "green",
        text: "At/Above Benchmark",
      },
    ],
    []
  );

  return (
    <div>
      <Row>
        <TitleWrap>
          Most Recent
          <br />
          Test
        </TitleWrap>
        <ColorBarWrap>
          <Red percent={getWith(redPercent)} />
          <Yellow percent={getWith(yellowPercent)} />
          <Blue percent={getWith(bluePercent)} />
          <Green percent={getWith(greenPercent)} />
          <ArrowImg num={num*10/13}>
            <img src={require("./assets/arrow.jpg")} alt="" />
          </ArrowImg>
          {numArr.map((v, i) => (
            <Num key={i} num={(v * 10) / 13}>
              {v}
            </Num>
          ))}
        </ColorBarWrap>
      </Row>
      <Row style={{ marginTop: 50 }}>
        {demoColor.map((v, i) => (
          <RowCenter style={{ margin: "0 8px" }} key={i}>
            <span
              style={{
                backgroundColor: v.color,
                width: 18,
                height: 18,
                display: "inline-block",
                borderRadius: 5,
                marginRight: 3,
              }}
            />
            {v.text}
          </RowCenter>
        ))}
      </Row>
    </div>
  );
};
